import { styled } from "@mui/material";
import chroma from "chroma-js";
import sizes from "./sizes";

export const Root = styled("div")(({ color, showingFullPalette }) => ({
  width: "20%",
  height: showingFullPalette ? "25%" : "50%",
  margin: "0 auto",
  display: "inline-block",
  position: "relative",
  cursor: "pointer",
  marginBottom: "-4px",
  background: color,
  "&:hover button": {
    opacity: 1,
  },
  [sizes.down("lg")]: {
    width: "25%",
    height: showingFullPalette ? "20%" : "33.3333%",
  },
  [sizes.down("md")]: {
    width: "50%",
    height: showingFullPalette ? "10%" : "20%",
  },
  [sizes.down("xs")]: {
    width: "100%",
    height: showingFullPalette ? "5%" : "10%",
  },
}));

export const BoxContent = styled("div")({
  position: "absolute",
  width: "100%",
  left: "0px",
  bottom: "0px",
  padding: "10px",
  color: "black",
  letterSpacing: "1px",
  textTransform: "uppercase",
  fontSize: "12px",
  boxSizing: "border-box",
});

export const CopyButton = styled("button")(({ color }) => ({
  color: chroma(color).luminance() >= 0.7 ? "rgba(0,0,0,0.6)" : "white",
  width: "100px",
  height: "30px",
  position: "absolute",
  display: "inline-block",
  top: "50%",
  left: "50%",
  marginLeft: "-50px",
  marginTop: "-15px",
  textAlign: "center",
  outline: "none",
  background: "rgba(255, 255, 255, 0.3)",
  fontSize: "1rem",
  lineHeight: "30px",
  textTransform: "uppercase",
  border: "none",
  textDecoration: "none",
  opacity: 0,
  cursor: "pointer",
}));

export const ColorName = styled("span")(({ color }) => ({
  color: chroma(color).luminance() <= 0.25 ? "white" : "black",
}));

export const SeeMore = styled("span")(({ color }) => ({
  color: chroma(color).luminance() >= 0.5 ? "rgba(0,0,0,0.6)" : "white",
  background: "rgba(255, 255, 255, 0.3)",
  position: "absolute",
  border: "none",
  right: "0px",
  bottom: "0px",
  width: "60px",
  height: "30px",
  textAlign: "center",
  lineHeight: "30px",
  textTransform: "uppercase",
}));

export const CopyText = styled("div")(({ color }) => ({
  color: chroma(color).luminance() >= 0.7 ? "black" : "white",
}));

export const CopyOverlay = styled("div")(({ color, copyOverlay }) => ({
  opacity: "0",
  zIndex: "0",
  width: "100%",
  height: "100%",
  transition: "transform 0.6s ease-in",
  transform: "scale(0.1)",
  background: color,

  ...(copyOverlay && {
    opacity: "1",
    transform: "scale(50)",
    zIndex: "10",
    position: "absolute",
  }),
}));

export const CopyMessage = styled("div")(({ copyOverlay }) => ({
  position: "fixed",
  left: "0",
  right: "0",
  top: "0",
  bottom: "0",
  display: "flex",
  alignItems: "center",
  justifyContent: "center",
  flexDirection: "column",
  fontSize: "4rem",
  transform: "scale(0.1)",
  opacity: "0",
  color: "white",
  "& h1": {
    fontWeight: "400",
    textShadow: "1px 2px black",
    background: "rgba(255, 255, 255, 0.2)",
    width: "100%",
    textAlign: "center",
    marginBottom: "0",
    padding: "1rem",
    textTransform: "uppercase",
    [sizes.down("xs")]: {
      fontSize: "6rem",
    },
  },
  "& p": {
    fontSize: "2rem",
    fontWeight: "100",
  },

  ...(copyOverlay && {
    opacity: "1",
    transform: "scale(1)",
    zIndex: "25",
    transition: "all 0.4s ease-in",
    transitionDelay: "0.3s",
  }),
}));
